/* Default styles */

.about__prickly>p {
    /* ... */
    width: 100%;

    /* Full width on smaller screens */

    /* ... */
}



/* Tablet styles */
@media screen and (width <= 768px) {
    .community__heading>h1 {
        font-size: 2.5rem;
    }

    .about__prickly>p {
        width: 100%;

        /* Restore original width on tablets */
    }
}




@media screen and (width <= 850px) {
    .feel__prickly>svg:nth-child(1) {
        transform: translateY(3%);
    }
}

@media screen and (width <= 950px) {
    /* .feel__prickly--content {
      margin-top: 10%;
    } */

    .feel__prickly {
        transform: translateY(10%);
    }

    .become__prick--div3 {
        min-height: 30vh;
        margin-bottom: 15%;
    }
}

@media screen and (width <= 600px) {
    .about__prickly h2,
    .about__prickly p {
        text-align: center;

        /* Adjust font sizes for smaller screens */
    }

    .feel__prickly {
        transform: translateY(15%);
    }

    .feel__prickly>svg:nth-child(1) {
        transform: translateY(4%);
    }

    .become__prick--div3 {
        margin-bottom: 30%;
    }
}

@media screen and (width <= 480px) {
    .become__prick--div2 {
        margin-left: 50px;
    }

    .community__heading {
        padding: 5% 3%;

        /* Further reduce padding on small screens */
    }

    /* .feel__prickly--content>p {
        margin-left: 100px;
    } */

    .main-nav__items a {
        font-size: 12px;
        margin-right: -6px;

    }

    .border__bottom {
        margin-left: 47px;
    }

    .main__header--pricks a {
        font-size: 18px;
        margin-right: 20px;
    }

    .community__heading>h1 {
        font-size: 1.8rem;
        margin-top: 100px;

        /* Adjust font size for smaller screens */
    }

    .about__prickly {
        padding: 5%;

        /* Increase padding on small screens */
    }

    .about__prickly h2,
    .about__prickly p {
        font-size: 1.5rem;
        text-align: center;

        /* Adjust font sizes for smaller screens */
    }

    .about__prickly>p {
        width: 100%;

        /* Full width on mobile */
    }

    .feel__prickly>svg:nth-child(1) {
        transform: translateY(5%);
    }

    .become__prick--div3 {
        margin-bottom: 50%;
    }
}