* {
  margin: 0;
  scroll-behavior: smooth;
}



/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  appearance: textfield;
}

.greenColor {
  color: green !important;
}

.redColor {
  color: red !important;
}