.productReviewsContainer {
  width: 100%;
  box-sizing: border-box;
  background-color: rgb(255 255 255);
  border-left: 1px solid rgb(0 0 0 / 15.8%);
  height: 100vh;
}

.productReviewsForm {
  width: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 3vmax;
  background-color: white;
}

.productReviewsFormHeading {
  color: rgb(0 0 0 / 73.3%);
  font: 300 2rem Roboto;
  text-align: center;
}

.productReviewsForm > div {
  display: flex;
  width: 100%;
  align-items: center;
  margin: 2rem;
}

.productReviewsForm > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgb(0 0 0 / 26.7%);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.productReviewsForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgb(0 0 0 / 62.3%);
}

@media screen and (width <= 600px) {
  .productReviewsContainer {
    border-left: none;
    border-top: 1px solid rgb(0 0 0 / 15.8%);
  }

  .productReviewsForm > div > input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .productReviewsForm > div > svg {
    font-size: 2.8vmax;
  }
}
